/**
 * 审批列表
 */
import { useEffect, useState, useMemo, useRef } from 'react';
import { getTestList } from './services';

import {
  Button, Tag, message, Drawer, Row, Col
} from 'antd';
import {
  FooterToolbar,
  ModalForm,
  PageContainer,
  ProDescriptions,
  ProFormText,
  ProFormTextArea,
  ProTable,
} from '@ant-design/pro-components';
import Service from './services';
const service = new Service();
export default () => {
  const actionRef = useRef();
  const [showDtl, setShowDtl] = useState(false);
  const [showSubDtl, setShowSubDtl] = useState(false)
  let statusList = [
    {
      label: '已处理',
      value: '1',
    },
    {
      label: '未处理',
      value: '2',
    },
    {
      label: '已取消',
      value: '3',
    },
  ];
  let [tsetData, setTsetData] = useState([
    {
      key: '1',
      customer: '测试客户A',
      approvalNumber: 'AP20240521001',
      orderNumber: 'ORD20240521001',
      type: '线上订单',
      amount: 268.50,
      time: '2024-05-21T10:30:00Z',
      status: '1',  // 已处理
      action: null  // 操作列无需数据
    },
    {
      key: '2',
      customer: '测试客户B',
      approvalNumber: 'AP20240521002',
      orderNumber: 'ORD20240521002',
      type: '线下订单',
      amount: 1580.00,
      time: '2024-05-21T14:15:00Z',
      status: '2',  // 未处理
      action: null
    },
    {
      key: '3',
      customer: '测试客户C',
      approvalNumber: 'AP20240521003',
      orderNumber: 'ORD20240521003',
      type: '退货申请',
      amount: -499.00,
      time: '2024-05-21T16:45:00Z',
      status: '3',  // 已取消
      action: null
    }
  ]);
  const paymentMethods = [
    { name: 'Apple Pay', id: 'apple_pay' },          // 修正建议：原"hpple Pay"
    { name: '支付宝', id: 'alipay' },
    { name: '信用卡', id: 'credit_card' },
    { name: 'Google Pay', id: 'google_pay' },
    { name: '+ Pay', id: 'plus_pay' },              // 需确认具体支付品牌
    { name: 'WApay', id: 'wa_pay' },                // 非常见支付方式，保留原名称
    { name: 'VISA', id: 'visa' },
    { name: 'GPay', id: 'g_pay' },                  // 可能与Google Pay重复
    { name: '银联', id: 'union_pay' },
    { name: 'PayPal', id: 'paypal' },               // 修正建议：原"GPyPal"
    { name: '微信支付', id: 'wechat_pay' },         // 修正建议：原"微情"
    { name: '雨点支付', id: 'yudian_pay' },         // 修正建议：原"优雨点"
    { name: '现金', id: 'cash' },
    { name: '支票', id: 'check' },
    { name: '银行转账', id: 'bank_transfer' },      // 修正建议：原"转瞰"
    { name: '饭卡', id: 'meal_card' },
    { name: 'SEPA', id: 'sepa' },                   // 欧盟单一欧元支付区
    { name: '饭票', id: 'meal_voucher' }
  ];
  let [tsetData2, setTsetData2] = useState({
    id: '1',
    product: '商品A',
    quantity: 2,
    taxRate: '10%',
    totalPrice: '110.00',
    orderType: '类型A',
    receiver: '收货人A',
    deliveryFee: '5.00',
    serviceFee: '5.00',
    productDiscount: '0.00',
    deduction: '0.00',
    finalTotal: '110.00',
  });
  let columns = [
    {
      title: '序号',
      valueType: 'index',
      width: 80,
    },
    {
      title: '客户',
      dataIndex: 'customer',
      width: 150,
      ellipsis: true,
    },
    {
      title: '审批编号',
      dataIndex: 'approvalNumber',
      width: 150,
      ellipsis: true,
      search: false,
    },
    {
      title: '订单编号',
      dataIndex: 'orderNumber',
      width: 150,
      ellipsis: true,
      search: false,
    },
    {
      title: '类型',
      dataIndex: 'type',
      width: 150,
      ellipsis: true,
      search: false,
    },
    {
      title: '金额',
      dataIndex: 'amount',
      width: 150,
      ellipsis: true,
      search: false,
    },
    {
      title: '时间',
      dataIndex: 'time',
      width: 150,
      ellipsis: true,
      search: false,
    },
    {
      title: '状态',
      dataIndex: 'status',
      width: 150,
      ellipsis: true,
      search: false,
      valueType: 'select',
      fieldProps: {
        options: statusList,
        showSearch: true,
        allowClear: true,
        showArrow: true,
      },
      render: (text: any, record: any) => (
        <>
          {
            record.status === '1' ? <Tag color="green">已处理</Tag> :
              record.status === '2' ? <Tag color="blue">未处理</Tag> :
                record.status === '3' ? <Tag color="red">已取消</Tag> : ''
          }
        </>
      ),
    },
    {
      title: '操作',
      dataIndex: 'action',
      width: 150,
      ellipsis: true,
      search: false,
      fixed: 'right',
      render: (text: any, record: any) => (
        <Button
          type="link"
          onClick={() => { setShowDtl(true); }}
        >
          查看
        </Button>
      ),
    },
  ];

  const columnsDtl = [
    {
      label: 'ID',
      dataIndex: 'id', // 使用有意义的 dataIndex
      ellipsis: true,
      width: 150,
    },
    {
      label: '商品',
      dataIndex: 'product', // 使用有意义的 dataIndex
      ellipsis: true,
      width: 150,
    },
    {
      label: '数量',
      dataIndex: 'quantity', // 使用有意义的 dataIndex
      ellipsis: true,
      width: 150,
    },
    {
      key: 'taxRate', // 修改为唯一的 key
      label: '税率',
      dataIndex: 'taxRate', // 使用有意义的 dataIndex
      ellipsis: true,
      width: 150,
    },
    {
      key: 'totalPrice', // 修改为唯一的 key
      label: '总价',
      dataIndex: 'totalPrice', // 使用有意义的 dataIndex
      ellipsis: true,
      width: 150,
    },
    {
      key: 'orderType', // 修改为唯一的 key
      label: '订单类型',
      dataIndex: 'orderType', // 使用有意义的 dataIndex
      ellipsis: true,
      width: 150,
      valueType: 'select',
      fieldProps: {
        options: statusList,
        showSearch: true,
        allowClear: true,
        showArrow: true,
      },
      render: (text: any, record: any) => (
        <>
          {record.status === '1' ? <Tag color="green">已处理</Tag> :
            record.status === '2' ? <Tag color="blue">未处理</Tag> :
              record.status === '3' ? <Tag color="red">已取消</Tag> : ''}
        </>
      ),
    },
    {
      key: 'receiver', // 修改为唯一的 key
      label: '收货人',
      dataIndex: 'receiver', // 使用有意义的 dataIndex
      ellipsis: true,
      width: 150,
    },
    {
      key: 'deliveryFee', // 修改为唯一的 key
      label: '配送费',
      dataIndex: 'deliveryFee', // 使用有意义的 dataIndex
      ellipsis: true,
      width: 150,
    },
    {
      key: 'serviceFee', // 修改为唯一的 key
      label: '服务费',
      dataIndex: 'serviceFee', // 使用有意义的 dataIndex
      ellipsis: true,
      width: 150,
    },
    {
      key: 'productDiscount', // 修改为唯一的 key
      label: '商品优惠',
      dataIndex: 'productDiscount', // 使用有意义的 dataIndex
      ellipsis: true,
      width: 150,
    },
    {
      key: 'deduction', // 修改为唯一的 key
      label: '减免',
      dataIndex: 'deduction', // 使用有意义的 dataIndex
      ellipsis: true,
      width: 150,
    },
    {
      key: 'finalTotal', // 修改为唯一的 key
      label: '总价',
      dataIndex: 'finalTotal', // 使用有意义的 dataIndex
      ellipsis: true,
      width: 150,
    },
  ];

  const items = [
    {
      key: 'ssstr', // 修改为唯一的 key
      label: 'ssstr',
      children: '1',
    },
    {
      key: 'product', // 修改为唯一的 key
      label: '商品',
      children: 'xxxx衣服',
    },
    {
      key: 'quantity', // 修改为唯一的 key
      label: '数量',
      children: '26',
    },
    {
      key: 'taxRate', // 修改为唯一的 key
      label: '税率',
      dataIndex: 'ssstr',
      ellipsis: true,
      search: true,
      width: 150,
    },
    {
      key: 'totalPrice', // 修改为唯一的 key
      label: '总价',
      dataIndex: 'ssstr',
      ellipsis: true,
      search: true,
      width: 150,
    },
    {
      key: 'orderType', // 修改为唯一的 key
      label: '订单类型',
      dataIndex: 'ssstr',
      ellipsis: true,
      search: true,
      width: 150,
    },
    {
      key: 'receiver', // 修改为唯一的 key
      label: '收货人',
      dataIndex: 'ssstr',
      ellipsis: true,
      search: true,
      width: 150,
    },
    {
      key: 'deliveryFee', // 修改为唯一的 key
      label: '配送费',
      dataIndex: 'ssstr',
      ellipsis: true,
      search: true,
      width: 150,
    },
    {
      key: 'serviceFee', // 修改为唯一的 key
      label: '服务费',
      dataIndex: 'ssstr',
      ellipsis: true,
      search: true,
      width: 150,
    },
    {
      key: 'productDiscount', // 修改为唯一的 key
      label: '商品优惠',
      dataIndex: 'ssstr',
      ellipsis: true,
      search: true,
      width: 150,
    },
    {
      key: 'deduction', // 修改为唯一的 key
      label: '减免',
      dataIndex: 'ssstr',
      ellipsis: true,
      search: true,
      width: 150,
    },
    {
      key: 'finalTotal', // 修改为唯一的 key
      label: '总价',
      dataIndex: 'ssstr',
      ellipsis: true,
      search: true,
      width: 150,
    },
  ];
  const test = () => {
    service.getTestList({
      page: 1,
      pageSize: 25,
      where: { "a.storeNo": "K7551" }
    }).then((res) => {
      console.log(res, 'res');

    });
  };
  useEffect(() => {
    test();
  }, []);
  return (
    <>
      <ProTable
        scroll={{ x: 'max-content' }}
        headerTitle="审批列表"
        actionRef={actionRef}
        rowKey="key"
        search={{
          labelWidth: 120,
        }}
        toolBarRender={() => [
          <Button
            type="primary"
            key="primary"
            onClick={() => {
              setShowDtl(true);
              test();
            }}
          >
            测试请求
          </Button>,
        ]}
        request={(params) => {
          // console.log(params);
          return Promise.resolve({
            data: tsetData,
            success: true,
            total: 3
          });
        }}
        columns={columns}
        rowSelection={{
          onChange: (_, selectedRows) => {

          },
        }}
      />
      {/* 详情 */}
      <Drawer
        title="详情"
        open={showDtl}
        onClose={() => { setShowDtl(false); }}
        width={800}>
        <ProDescriptions
          dataSource={tsetData2}
          // request={(params) => {
          //   return Promise.resolve({
          //     data: tsetData2,
          //     success: true,
          //   });
          // }}
          columns={columnsDtl}
        />
        <br />
        <Button type="primary" onClick={() => {
          setShowDtl(false);
          setShowSubDtl(true);
        }}>查看进度</Button>
      </Drawer>
      {/*  */}
      <ModalForm
        title="xxxx订单"
        open={showSubDtl}
        modalProps={{
          onCancel: () => {
            setShowSubDtl(false);
          },
        }}
        onFinish={(fmt) => {
          console.log(fmt);
          message.success('操作成功');
          setShowSubDtl(false);
        }}
        width={'70%'}
      >
        <Row gutter={16}>
          <Col span={8}>
            <ProFormText
              disabled
              name="msg"
              label="商品"
              placeholder={''}
            />
          </Col>
          <Col span={8}>
            <ProFormText
              disabled
              name="msg"
              label="数量"
              placeholder={''}
            />
          </Col>
          <Col span={8}>
            <ProFormText
              disabled
              name="msg"
              label="价格"
              placeholder={''}
            />
          </Col>
        </Row>
        <Row gutter={16}>
          {
            paymentMethods.map((item, index) => {
              return (
                <Col span={6} key={item.id}>
                  <div style={{
                    padding: '5px',
                    paddingBottom: 0,
                    boxShadow: '0px 2px 5px rgba(0,0,0,0.3)',
                    borderRadius: '5px',
                    margin: '5px',
                  }}>
                    <ProFormText
                      fieldProps={{
                        type: "number",
                        min: 0,
                        step: 0.01,
                        style: {
                          width: '100%',
                          marginBottom: '5px'
                        }
                      }}
                      name={"msg" + index}
                      label={
                        <div
                          style={{
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'space-between',
                            padding: '5px ',
                          }}
                        >
                          <img
                            style={{ width: '30px', height: '30px', marginRight: '10px' }}
                            src="https://gw.alipayobjects.com/zos/bmw-prod/2dd637c7-5f50-4d89-a819-33b3d6da73b6.svg"
                            alt="" />
                          <div>{item.name}</div>
                        </div>
                      }
                    />
                  </div>
                </Col>
              );
            })
          }
        </Row>
      </ModalForm >
    </>
  );
};